<template>
	<div>
		<el-container>
			<el-header>
				<headerNav />
			</el-header>
			<el-main>
				<div class="left">
					<div class="title"></div>
					<div class="content" v-show="loginTips">
						 
					</div>
					<div v-show="regTips">
						
					</div>
					<div v-show="forgetPwdTips">
						
					</div>
				</div>
				<div class="right" v-show="loginShow">
					<el-tabs :stretch="true" v-model="activeName" @tab-click="handleClick">
						<el-input maxlength="11" v-model="phone" placeholder=""></el-input>
						<el-tab-pane label="" name="first">
							<el-input type="password" v-model="pass" minlength="6" placeholder=""></el-input>
						</el-tab-pane>
						<el-tab-pane label="" name="second">
							<el-input maxlength="6" v-model="checkCode" placeholder="">
								<template slot="append">
									<el-button @click="validateBtn" style="color: #409EFF;">{{btnTitle}}</el-button>
								</template>
							</el-input>
						</el-tab-pane>
						<el-button type="primary" class="loginBut" @click="sub">{{loginText}}</el-button>
					</el-tabs>
					<div class="forgetPwd" @click="forgetPwd"></div>
				</div>
				<div class="right" v-show="forgetPwdShow">
					<div class="title"></div>
					<el-input maxlength="11" v-model="FPphone" placeholder="" />
					<el-input maxlength="6" v-model="FPcheckCode" placeholder="">
						<template slot="append">
							<el-button @click="validateFPBtn" style="color: #409EFF;">{{PFbtnTitle}}</el-button>
						</template>
					</el-input>
					<el-input type="password" v-model="newPass" minlength="6" placeholder=""></el-input>
					<el-button type="primary" class="loginBut" @click="update"></el-button>
					<div class="login-type">
						<div class="pwd-phone-login" @click="PwdLogin"></div>
						<div class="pwd-phone-login" @click="PhoneLogin"></div>
					</div>
				</div>
			</el-main>
		</el-container>
	</div>
</template>

<script>
	import headerNav from "../components/headerNav.vue";
	import {
		Form,
		Button,
		Input,
		FormItem,
		Container,
		Header,
		Main,
		Tabs,
		TabPane,
		Message
	} from 'element-ui';
	export default {
		components: {
			headerNav,
			[Form.name]: Form,
			[Button.name]: Button,
			[Input.name]: Input,
			[FormItem.name]: FormItem,
			[Container.name]: Container,
			[Header.name]: Header,
			[Main.name]: Main,
			[Tabs.name]: Tabs,
			[TabPane.name]: TabPane,
			[Message.name]: Message,
		},
		data() {
			return {
				activeName: 'first',
				btnTitle: '',
				PFbtnTitle: '',
				phone: '',
				pass: '',
				checkCode: '',
				loginText: '',
				loginTips: true,
				regTips: false,
				forgetPwdTips:false,
				loginShow: true,
				forgetPwdShow: false,
				// 
				FPphone: '',
				FPcheckCode: '',
				newPass: ''
			};
		},
		methods: {
			// 
			validateBtn() {
				let time = 6;
				let timer = setInterval(() => {
					// console.log(time);
					if (time == 0) {
						clearInterval(timer);
						this.btnTitle = "";
					} else {
						this.btnTitle = time + '';
						time--
					}
				}, 1000)
			},
			//  
			validateFPBtn(){
				let phoneTime = 6;
				let timer = setInterval(() => {
					// console.log(time);
					if (phoneTime == 0) {
						clearInterval(timer);
						this.PFbtnTitle = "";
					} else {
						this.PFbtnTitle = phoneTime + '';
						phoneTime--
					}
				}, 1000)
			},
			// 
			sub() {
				if (this.activeName == "first") {
					if (this.phone == "") {
						Message.error('');

					} else if (this.pass == "") {
						Message.error('');
					} else if (!/^1[3456789]\d{9}$/.test(this.phone)) {
						Message.error('');
					} else if (!/^.{6,}$/.test(this.pass)) {
						Message.error('');
					} else {
						this.loginText = "...";
						setTimeout(() => {
							this.$router.push('/my_account');
							sessionStorage.setItem("loginK", true);
						}, 1000);
					}
				}
				if (this.activeName == "second") {
					if (this.phone == "") {
						Message.error('');

					} else if (!/^1[3456789]\d{9}$/.test(this.phone)) {
						Message.error('');
					} else if (this.checkCode == "") {
						Message.error('');
					} else if (!/^\d{6}$/.test(this.checkCode)) {
						Message.error('');
					} else {
						this.loginText = "...";
						setTimeout(() => {
							this.$router.push('/my_account');
						}, 1000);
					}
				}
			},
			// 
			handleClick() {
				if (this.activeName == "first") {
					this.loginTips = true;
					this.regTips = false;
				}
				if (this.activeName == "second") {
					this.loginTips = false;
					this.regTips = true;
				}
			},
			// 
			forgetPwd() {
				// 
				this.loginShow = false;
				this.forgetPwdShow = true;	
				// 
				this.loginTips = false;
				this.regTips = false;
				this.forgetPwdTips = true;
			},
			// 
			update() {
				if (this.FPphone == "") {
					Message.error('');

				} else if (!/^1[3456789]\d{9}$/.test(this.FPphone)) {
					Message.error('');
				} else if (this.FPcheckCode == "") {
					Message.error('');
				} else if (!/^\d{6}$/.test(this.FPcheckCode)) {
					Message.error('');
				} else if (this.pass == "") {
					Message.error('');
				} else if (!/^.{6,}$/.test(this.newPass)) {
					Message.error('');
				} else {
					Message.success('');
					setTimeout(() => {
						this.PwdLogin();
					}, 3000);
				}
			},
			// 
			PwdLogin() {
				this.loginShow = true;
				this.forgetPwdShow = false;
				this.activeName = "first";
				// 
				this.loginTips = true;
				this.regTips = false;
				this.forgetPwdTips = false;
			},
			// 
			PhoneLogin() {
				this.loginShow = true;
				this.forgetPwdShow = false;
				this.activeName = "second";
				// 
				this.loginTips = false;
				this.regTips = true;
				this.forgetPwdTips = false;
			}
		}
	}
</script>

<style scoped>
	/*  */
	.el-main {
		width: 911px;
		margin: 100px auto 0;
		background-color: #fff;
		box-shadow: 0 0 20px 0 rgba(51, 51, 51, 0.20);
		display: flex;
		height: 400px;
		padding: 0;
	}


	/*  */
	.el-main .left {
		width: 50%;
		background-color: #57BEC3;
		color: #fff;
		text-align: center;
	}

	/*  */
	.left .title {
		height: 60px;
		line-height: 60px;
		font-size: 20px;
	}

	.left .content {
		width: 80%;
		margin: 10px auto 0;
		text-align: left;
		line-height: 30px;
	}

	/*  */
	.el-main .right {
		width: 50%;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
	}



	/*  */

	.right>>>.el-tabs__header {
		display: flex;
		justify-content: center;
		margin-bottom: 40px;
	}

	.right>>>.el-tabs__nav-wrap {
		width: 100%;
	}

	/*  */
	.el-input {
		width: 350px;
		margin-bottom: 20px;
	}

	/*  */
	.loginBut {
		margin-top: 10px;
		width: 350px;
		font-size: 18px;
	}

	/*  */
	.forgetPwd {
		margin-top: 20px;
		color: #005980;
		cursor: pointer;
	}

	.right .title {
		margin-bottom: 30px;
		font-size: 20px;
		font-weight: 600;
		color: #333333;
	}

	/*  */
	.login-type {
		display: flex;
		justify-content: space-between;
		width: 100%;
		margin-top: 20px;
		width: 350px;
	}
	.login-type .pwd-phone-login {
		cursor: pointer;
		color: #005980;
	}
</style>
