import Vue from "vue";
import VueRouter from "vue-router";

//  
import index from "../views/index.vue"; //

import Tourism from "../views/Community/Tourism.vue"; //
import otherdetails from "../views/Community/otherdetails.vue";//-
import indexdetails from "../views/Community/indexdetails.vue";//-
import otherMeans from "../views/Community/otherMeans.vue";//-
import issueOpus from "../views/Community/issueOpus.vue";//-
import follow from "../views/Community/follow.vue";//-

import commodity_index from "../views/commodityCenter/commodity_index.vue"; //
import commodity_details from "../views/commodityCenter/commodity_details.vue"; //
import commodity_order from "../views/commodityCenter/commodity_order.vue"; //
import commodity_foodpage from "../views/commodityCenter/commodity_foodpage"; /* */
import commodity_payment from "../views/commodityCenter/commodity_payment.vue"; /* */
import commodity_accomplish from "../views/commodityCenter/commodity_accomplish.vue"; /* */


import AboutUs from "../views/aboutus/AboutUs.vue"; //
import Download from "../views/Download/Download.vue"; //app
import login from "../views/login.vue"; //
import my_account from "../views/my_account.vue"; //
import my_order from "../views/my_order.vue"; //
import my_set from "../views/my_set.vue"; //

import shopCart from "../views/shopCart.vue";//


Vue.use(VueRouter);

const routes = [
    {
        path:"/",//
        component:index //
    }, 
    {
        path:'/index',//
        component:index //z
    },
    {
        path:'/shopCart', 
        component:shopCart 
    },
    {
        path:'/AboutUs', 
        component:AboutUs 
    },
    {
        path:'/Tourism', 
        component:Tourism 
    },
    {
        path:'/indexdetails/:aid', 
        component:indexdetails 
    },
    {
        path:'/otherdetails/:aid', 
        component:otherdetails 
    },
    {
        path:'/otherMeans', 
        component:otherMeans 
    },
    {
        path:'/commodity_index', 
        component:commodity_index 
    },
    {
        path:'/commodity_details', 
        component:commodity_details 
    },
    {
        path:'/commodity_order', 
        component:commodity_order 
    },
    {
        path:'/Download', 
        component:Download 
    },
    {
		path: '/login',
		component: login //
	},
	{
		path: '/my_account',
		component: my_account //
	},
	{
		path: '/my_order',
		component: my_order //
	},
	{
		path: '/my_set',
		component: my_set 
    },
    {
		path: '/issueOpus',
		component: issueOpus 
    },
    {
       path: '/follow',
       component: follow 
    },
    {
       path: '/commodity_foodpage',
       component: commodity_foodpage 
    },
    {
       path: '/commodity_payment',
       component: commodity_payment 
    },
    {
       path: '/commodity_accomplish',
       component: commodity_accomplish 
    }
    
   
]
var router = new VueRouter({
    routes
})

export default router;