<template>
    <div id="commodity_order">
        <el-container>
            <el-header>
                <headerNav />
            </el-header>
            <div class="main">
                    <div class="order_process">
                        <el-steps :active="0" simple finish-status="success">
                            <el-step title="" icon="el-icon-edit"></el-step>
                            <el-step title="" icon="el-icon-upload"></el-step>
                            <el-step title="" icon="el-icon-picture"></el-step>
                            <el-step title="" icon="el-icon-picture"></el-step>
                        </el-steps>
                    </div>
                    <div class="address">
                        <p> </p>
                        <span><i class="el-icon-s-promotion"></i> </span>
                    </div>
                    <div class="detailed_information">
                        <div v-for="(use,k) in userinfo" :key="k" class="tfo">
                            <div class="ddin" v-bind:class="{ddin2: use.a}" v-if="use.reveal">
                                <div @click="choice(k)">
                                    <h1>{{use.consignee}}</h1>
                                    <el-divider></el-divider>
                                    <p>{{use.shipping}}</p>
                                </div>
                                <p v-if="use.modification==true" @click="dialogFormVisible = true" style="color: #FF0036;"></p>
                            </div>
                        </div>
                        <div>
                            <!-- Form -->
                            <el-dialog title="" :visible.sync="dialogFormVisible">
                                <el-form :model="form">
                                    <el-form-item label="" :label-width="formLabelWidth">
                                        <el-input v-model="form.name" autocomplete="off"></el-input>
                                    </el-form-item>
                                    <el-form-item label="" :label-width="formLabelWidth">
                                        <el-input v-model="form.region" autocomplete="off"></el-input>
                                    </el-form-item>
                                </el-form>
                                <div slot="footer" class="dialog-footer">
                                    <el-button @click="dialogFormVisible = false"> </el-button>
                                    <el-button type="primary" @click="dialogFormVisible = false"> </el-button>
                                </div>
                            </el-dialog>
                        </div>
                    </div>
                    <div class="operator">
                        <p @click="dizhi" v-if="daa"></p>
                        <p @click="hta" v-if="!daa"></p>
                        <p></p>
                    </div>
                    <div class="confirmation_message ">
                        <p></p>
                        <div class="cnme">
                            <el-row>
                                <el-col :span="7"></el-col>
                                <el-col :span="3"></el-col>
                                <el-col :span="2"></el-col>
                                <el-col :span="4"></el-col>
                                <el-col :span="4"></el-col>
                                <el-col :span="4"></el-col>
                            </el-row>
                            <el-divider></el-divider>
                        </div>
                        <p> <i class="el-icon-platform-eleme"></i> : </p>
                        <el-divider></el-divider>
                        <el-row id="sp">
                            <el-col :span="7">
                                <el-col :span="8">
                                    <img src="../../assets/images/commodityCenter/meishi3.jpg" height="100px" width="90px" />
                                </el-col>
                                <el-col :span="15"> 60g*5
                                </el-col>
                            </el-col>
                            <el-col :span="3"></el-col>
                            <el-col :span="2">{{price}}.00</el-col>
                            <el-col :span="4">
                                <el-input-number v-model="num" @change="handleChange" :min="1" :max="10" label=""></el-input-number>
                            </el-col>
                            <el-col :span="4">113:</el-col>
                            <el-col :span="4">{{total}}.00</el-col>
                        </el-row>
                        <el-divider></el-divider>
                        <el-row>
                            <el-col :span="12">
                                <el-checkbox v-model="checked"></el-checkbox>
                            </el-col>
                            <el-col :span="12">
                                <el-col :span="20"> </el-col>
                                <el-col :span="4">0.00</el-col>
                            </el-col>
                        </el-row>
                        <el-divider></el-divider>
                        <el-row>
                            <el-col :span="12">

                                <el-col :span="6">
                                    
                                </el-col>
                                <el-col :span="16">
                                    <el-input type="textarea" placeholder="" v-model="textarea" maxlength="100"
                                        show-word-limit>
                                    </el-input>
                                </el-col>
                            </el-col>
                            <el-col :span="12">
                                <el-col :span="20">
                                    <el-checkbox v-model="checked1"> 
                                    </el-checkbox>
                                </el-col>
                                <el-col :span="4">0.00</el-col>
                            </el-col>
                        </el-row>
                        <el-divider></el-divider>
                        <el-row>
                            <el-col :span="24">(){{total}}.00</el-col>
                        </el-row>
                        <el-divider></el-divider>
                        <el-row type="flex" class="row-bg" justify="space-between">
                            <el-col :span="11">
                                <span></span>
                                <span style="color: #FF0000; font-size: 18px;">{{total}}.00</span>
                                <p><span></span><span>     8217</span></p>
                                <p><span></span><span>  15077399401 </span></p>
                            </el-col>
                            <el-col :span="5">
                                <el-row>
                                    <el-button type="danger" round @click="submit()"></el-button>
                                </el-row>
                            </el-col>

                        </el-row>
                    </div>
            </div>
            <div style="height:60px"></div>
        </el-container>
        <!-- <el-main>Main -->
    </div>
</template>

<script>
    import headerNav from "../../components/headerNav.vue";
    import {
        container,
        header,
        main,
        steps,
        step,
        divider,
        row,
        col,
        InputNumber,
        checkbox,
        input,
        button,
        dialog,
        table,
        TableColumn,
        form,
        FormItem,
        select,
        option
    } from 'element-ui';
    export default {
        data() {
            return {
                /*  */
                daa: true,
                active: 0,
                num: 1,
                price: 215,
                total: 215,
                checked: false,
                checked1: false,

                textarea: '',
                dialogFormVisible: false,
                form: {
                    name: '',
                    region: '',
                    date1: '',
                    date2: '',
                    delivery: false,
                    type: [],
                    resource: '',
                    desc: ''
                },
                formLabelWidth: '120px',
                userinfo: [{
                        reveal: true,
                        modification: true,
                        a: true,
                        consignee: '',
                        shipping: '8217 15077399401',
                    },
                    {
                        reveal: true,
                        modification: false,
                        a: false,
                        consignee: '',
                        shipping: '8217 15077399401',
                    },
                    {
                        reveal: true,
                        modification: false,
                        a: false,
                        consignee: '',
                        shipping: '8217 15077399401',
                    },
                    {
                        reveal: false,
                        modification: false,
                        a: false,
                        consignee: '',
                        shipping: '8217 15077399401',
                    },
                    {
                        reveal: false,
                        modification: false,
                        a: false,
                        consignee: '',
                        shipping: '8217 15077399401',
                    },
                ],

            };
        },
        components: {
            headerNav,
            [container.name]: container,
            [header.name]: header,
            [main.name]: main,
            [steps.name]: steps,
            [step.name]: step,
            [divider.name]: divider,
            [row.name]: row,
            [col.name]: col,
            [InputNumber.name]: InputNumber,
            [checkbox.name]: checkbox,
            [input.name]: input,
            [button.name]: button,
            [dialog.name]: dialog,
            [table.name]: table,
            [TableColumn.name]: TableColumn,
            [form.name]: form,
            [FormItem.name]: FormItem,
            [select.name]: select,
            [option.name]: option,

        },
        methods: {
            /*  */

            handleChange() {
                let num = this.num

                let price = this.price
                this.total = price * num
            },
            /*  */
            choice(k) {
                this.modification = true;
                for (var i = 0; i < this.userinfo.length; i++) {
                    if (k == i) {
                        this.userinfo[k].a = true;
                        this.userinfo[k].modification = true;
                    } else {
                        this.userinfo[i].a = false;
                        this.userinfo[i].modification = false;
                    }
                }
            },
            /*  */
            dizhi() {
                this.daa = false;
                for (var i = 0; i < this.userinfo.length; i++) {
                    this.userinfo[i].reveal = true;
                }
            },
            /*  */
            hta() {
                this.daa = true;
                for (var i = 0; i < this.userinfo.length; i++) {
                    if (i > 2) {
                        this.userinfo[i].reveal = false;
                    }
                }
            },
            submit(){
                 this.$router.push('/commodity_payment')
            }
        }
    }
</script>

