<template>
	<div class="my_account">
		<el-container>
			<el-header>
				<headerNav />
			</el-header>
			<el-main>
				<pbLeft />
				<div class="right">
					<div class="balance">
						<span class="title"></span>
						<span class="money">{{allMoney}}</span>
						<el-dialog width="30%" title="" :close-on-click-modal="false" :lock-scroll="false" :visible.sync="dialogTableVisible">
							<el-input placeholder="" maxlength="13" v-model="money">
								<template slot="prepend">:</template>
							</el-input>
							<el-dialog :close-on-click-modal="false" title="" :visible.sync="innerVisible" append-to-body>
								<div style="position: absolute;top: 23px;left: 150px; font-size: 16px;"><span style="color: red; font-size: 17px;font-weight: bold;">{{money}}</span></div>
								<div class="PayPal">
									<el-tabs :stretch="true" type="border-card" v-model="activeName">
										<el-tab-pane label="" name="first">
											<el-row>
												<div @click="choice(k)" v-for="(bank,k) in bank_card" :key="k">
													<el-col :span="5" v-bind:class="{zfbk: bank.zfbk}">
														<img :src="bank.logo" />
														<p> {{bank.text}}</p>
													</el-col>
												</div>
											</el-row>
										</el-tab-pane>
										<el-tab-pane label="">
											<el-row>
												<div @click="terrace(i)" v-for="(pay,i) in paymentList" :key="i">
													<el-col :span="5" v-bind:class="{zfbk: pay.zfbk}">
														<img :src="pay.logo" />
														<p> {{pay.text}}</p>
													</el-col>
												</div>
											</el-row>
										</el-tab-pane>
										<el-row v-if="activeName!='Add'">
											<el-divider></el-divider>
											<el-button type="danger" :disabled="disabled" @click="rechargeNow"></el-button>
											<span v-if="hint"></span>
										</el-row>
									</el-tabs>
								</div>
							</el-dialog>
							<div slot="footer" class="dialog-footer">
								<el-button type="primary" @click="nextStep"></el-button>
							</div>
						</el-dialog>
						<el-button type="primary" class="but_r" @click="dialogTableVisible = true"></el-button>
						<el-button type="primary" class="but_w" @click="WithdrawalShow = true"></el-button>
						<el-dialog :close-on-click-modal="false" title="" :visible.sync="WithdrawalShow" append-to-body>
							<div class="PayPal">
								<div style="position: absolute;top: 23px;left: 150px; font-size: 16px;"><span style="color: red; font-size: 17px;font-weight: bold;">{{allMoney}}</span></div>
								<el-row>
									<div style="margin-bottom: 20px;margin-left: 20px;">
										<el-input placeholder="" maxlength="13" v-model="Withdrawal">
											<template slot="prepend">:</template>
										</el-input>
									</div>
								</el-row>
								<el-row>
									<div @click="choice(k)" v-for="(bank,k) in bank_card" :key="k">
										<el-col :span="5" v-bind:class="{zfbk: bank.zfbk}">
											<img :src="bank.logo" />
											<p> {{bank.text}}</p>
										</el-col>
									</div>
								</el-row>
								<el-row v-if="activeName!='Add'">
									<el-divider></el-divider>
									<el-button type="danger" :disabled="disabled" @click="WithdrawalNow"></el-button>
									<span v-if="hint"></span>
								</el-row>
							</div>
						</el-dialog>
					</div>
					<el-tabs type="border-card" :stretch="true">
						<el-tab-pane label="">
							<el-table :data="czTable" style="width: 100%">
								<el-table-column prop="czDate" label="" width="180">
								</el-table-column>
								<el-table-column prop="czType" label="" width="180">
								</el-table-column>
								<el-table-column prop="czMoney" label="()">
								</el-table-column>
								<el-table-column prop="czState" label="">
								</el-table-column>
							</el-table>
						</el-tab-pane>
						<el-tab-pane label="">
							<el-table :data="txTable" style="width: 100%">
								<el-table-column prop="txDate" label="" width="180">
								</el-table-column>
								<el-table-column prop="txType" label="" width="180">
								</el-table-column>
								<el-table-column prop="txMoney" label="()">
								</el-table-column>
								<el-table-column prop="txState" label="">
								</el-table-column>
							</el-table>
						</el-tab-pane>
					</el-tabs>
					<div class="bankCard">
						<el-row>
							<el-col :span="3">
								<div class="title grid-content"></div>
							</el-col>
							<el-col :span="8">
								<div class="grid-content bankNum">
									<span>{{CCnum}}</span><span>{{DCnum}}</span>
								</div>
							</el-col>
						</el-row>
						<el-row :gutter="20">
							<el-col :span="12" v-for="(val,k) in bankCardList" :key="k">
								<div @mouseover="mouseOver(k)" @mouseleave="mouseLeave(k)" class="grid-content bg-purple bankCardList">
									<el-row style="background-color: #fcfcfc; padding: 10px 20px;">
										<el-col :span="2">
											<div><img height="25px" width="25px" style="margin-top: 5.5px;" :src="require('../assets/images/commodityCenter/'+val.logo+'')" /></div>
										</el-col>
										<el-col :span="8">
											<div>{{val.bankName}}</div>
										</el-col>
										<el-col :span="6">
											<div>
												{{val.num.substr(val.num.length-4,4)}}
											</div>
										</el-col>
										<el-col :span="5">
											<div>
												{{val.type}}
											</div>
										</el-col>
									</el-row>
									<div style="padding: 20px 25px;">
										<el-row type="flex" justify="space-between">
											<el-col :span="12">
												<div>*{{val.userName.substr(1,)}}</div>
											</el-col>
											<el-col v-show="val.operation" style="text-align: right;color: #57BEC3;cursor: pointer;" :span="4">
												<div></div>
											</el-col>
										</el-row>
										<el-row type="flex" justify="space-between">
											<el-col :span="12">
												<div>{{val.phone.substr(0,3)+"****"+val.phone.substr(val.phone.length-4,4)}}</div>
											</el-col>
											<el-col v-show="val.operation" style="text-align: right;color: #57BEC3;cursor: pointer;" :span="4">
												<div @click="deleBankCard(k)"></div>
											</el-col>
										</el-row>
									</div>
								</div>
							</el-col>
							<el-col :span="12">
								<div id="bankCardBut" @click="bankCardAddShow = true">
									<div><i class="el-icon-plus"></i></div>
									<div></div>
								</div>
							</el-col>
							<el-dialog title="" :close-on-click-modal="false" :visible.sync="bankCardAddShow">
								<el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
									<el-form-item label="" prop="name">
										<el-input v-model="ruleForm.name" placeholder=""></el-input>
									</el-form-item>
									<el-form-item label="" prop="identity">
										<el-input v-model="ruleForm.identity" placeholder=""></el-input>
									</el-form-item>
									<el-form-item label="" prop="card">
										<el-input v-model="ruleForm.card" placeholder=""></el-input>
									</el-form-item>
									<el-form-item label="" prop="phone">
										<el-input v-model="ruleForm.phone" placeholder=""></el-input>
									</el-form-item>
									<el-form-item label="" prop="yzm">
										<div id="yzm" style="display: flex;">
											<el-input v-model="ruleForm.yzm" placeholder="">
											</el-input>
											<el-button type="primary"></el-button>
										</div>
									</el-form-item>
									<el-form-item>
										<el-button type="primary" @click="submitForm('ruleForm')"></el-button>
										<el-button @click="resetForm('ruleForm')"></el-button>
									</el-form-item>
								</el-form>
							</el-dialog>
						</el-row>
					</div>
				</div>
			</el-main>
		</el-container>
	</div>
</template>

<script>
	import headerNav from "../components/headerNav.vue";
	import pbLeft from "../components/pbLeft.vue";
	import {
		Header,
		Button,
		Input,
		Main,
		Container,
		// 
		tabs,
		TabPane,
		// 
		Table,
		TableColumn,
		Dialog,
		Row,
		Col,
		Form,
		FormItem,
		Divider,
		Message,
		MessageBox,
		Notification
	} from 'element-ui';
	export default {
		components: {
			headerNav,
			pbLeft,
			[Header.name]: Header,
			[Button.name]: Button,
			[Input.name]: Input,
			[Main.name]: Main,
			[Container.name]: Container,
			// 
			[tabs.name]: tabs,
			[TabPane.name]: TabPane,
			// 
			[Table.name]: Table,
			[TableColumn.name]: TableColumn,
			[Dialog.name]: Dialog,
			[Row.name]: Row,
			[Col.name]: Col,
			[Form.name]: Form,
			[FormItem.name]: FormItem,
			[Divider.name]: Divider,
			[Message.name]: Message,
			[MessageBox.name]: MessageBox,
			[Notification.name]: Notification,
		},
		data() {
			return {
				// 
				czTable: [{
						czDate: '2020-07-02',
						czType: '',
						czMoney: '2000',
						czState: ''
					}, {
						czDate: '2020-05-04',
						czType: '',
						czMoney: '100',
						czState: ''
					},
					{
						czDate: '2020-01-04',
						czType: '',
						czMoney: '5000',
						czState: ''
					},
				],
				txTable: [{
						txDate: '2020-9-02',
						txType: '',
						txMoney: '2000',
						txState: ''
					}, {
						txDate: '2020-05-04',
						txType: '',
						txMoney: '3000',
						txState: ''
					},
					{
						txDate: '2020-02-04',
						txType: '',
						txMoney: '1000',
						txState: ''
					},
				],
				allMoney: 6000,
				dialogTableVisible: false,
				WithdrawalShow: false,
				outerVisible: false,
				innerVisible: false,
				money: '',
				Withdrawal:'',
				//
				disabled: true,
				activeName: 'first',
				/*  */
				hint: true,
				bank_card: [{
						zfbk: false,
						logo: require('../assets/images/logo.png'),
						text: '',
					},

					{
						zfbk: false,
						logo: require('../assets/images/commodityCenter/zgyh.jpg'),
						text: ' ',
					},
					{
						zfbk: false,
						logo: require('../assets/images/commodityCenter/youzheng.jpg'),
						text: '',
					},
					{
						zfbk: false,
						logo: require('../assets/images/commodityCenter/zg.jpg'),
						text: '',
					},
					{
						zfbk: false,
						logo: require('../assets/images/commodityCenter/zn.jpg'),
						text: '',
					},
				],
				paymentList: [{
						zfbk: false,
						logo: require('../assets/images/logo.png'),
						text: '',
					},
					{
						zfbk: false,
						logo: require('../assets/images/commodityCenter/weixin.jpg'),
						text: '',
					},
					{
						zfbk: false,
						logo: require('../assets/images/commodityCenter/zhifubao.jpg'),
						text: '',
					},
				],
				ruleForm: {
					/*  */
					name: '',
					/*  */
					identity: '',
					/*  */
					card: '',
					/*  */
					phone: '',
					/*  */
					yzm: '',
				},
				rules: {
					name: [{
							required: true,
							message: '',
							trigger: 'blur'
						},
						{
							min: 3,
							max: 5,
							message: ' 3  5 ',
							trigger: 'blur'
						}
					],
					identity: [{
							required: true,
							message: '',
							trigger: 'blur'
						},
						{
							min: 18,
							max: 18,
							message: '',
							trigger: 'blur'
						}
					],
					card: [{
							required: true,
							message: '',
							trigger: 'blur'
						},
						{
							min: 15,
							max: 19,
							message: '',
							trigger: 'blur'
						},
						/* {
						    type: 'number',
						    message: '',
						    trigger: 'blur',
						} */
					],
					phone: [{
							required: true,
							message: '',
							trigger: 'blur'
						},
						{
							min: 11,
							max: 11,
							message: '',
							trigger: 'blur'
						},
						/* {
						    type: 'number',
						    message: '',
						    trigger: 'blur',
						} */
					],
					yzm: [{
							required: true,
							message: '',
							trigger: 'blur'
						},

					],
				},
				//
				DCnum: 0,
				CCnum: 0,
				bankCardList: [{
						logo: 'zn.jpg',
						bankName: '',
						num: '6228480831781064918',
						type: '',
						userName: '',
						phone: '18445156052',
						operation: false
					},
					{
						logo: 'zgyh.jpg',
						bankName: '',
						num: '6228480831781068644',
						type: '',
						userName: '',
						phone: '17926564587',
						operation: false
					},
					{
						logo: 'zg.jpg',
						bankName: '',
						num: '6228480831781065241',
						type: '',
						userName: '',
						phone: '18926568696',
						operation: false
					},
					{
						logo: 'youzheng.jpg',
						bankName: '',
						num: '6217996100080404583',
						type: '',
						userName: '',
						phone: '17926563245',
						operation: false
					}
				],
				// 
				bankCardAddShow: false,
			};
		},
		mounted() {
			// 
			let DCnum = 0;
			let CCnum = 0;
			for (let i = 0; i < this.bankCardList.length; i++) {
				if (this.bankCardList[i].type == "") {
					DCnum++
				}
				if (this.bankCardList[i].type == "") {
					CCnum++
				}
			}
			this.DCnum = DCnum;
			this.CCnum = CCnum;
		},
		methods: {
			// 
			nextStep() {
				let reg = /(^[1-9]([0-9]+)?(\.[0-9]{1,2})?$)|(^(0){1}$)|(^[0-9]\.[0-9]([0-9])?$)/;
				if (reg.test(this.money) && parseFloat(this.money) != 0) {
					this.innerVisible = true;
				} else {
					Notification.error({
						title: '',
						showClose: false,
						duration: 3000,
					});
				}
			},
			// 
			rechargeNow() {
				this.innerVisible = false;
				this.dialogTableVisible = false;
				this.allMoney = parseFloat(this.money) + parseFloat(this.allMoney);
				Message.success('');
			},
			// 
			WithdrawalNow() {
				let reg = /(^[1-9]([0-9]+)?(\.[0-9]{1,2})?$)|(^(0){1}$)|(^[0-9]\.[0-9]([0-9])?$)/;
				if (reg.test(this.Withdrawal) && parseFloat(this.Withdrawal) != 0 && !(this.Withdrawal>this.allMoney)) {
					this.WithdrawalShow = false;
					this.allMoney = parseFloat(this.allMoney) - parseFloat(this.Withdrawal);
					Message.success('');
				} else {
					Notification.error({
						title: '',
						showClose: false,
						duration: 3000,
					});
				}
				
			},
			// 
			submitForm(formName) {
				this.$refs[formName].validate((valid) => {
					if (valid) {
						Message.success('');
						this.bankCardAddShow = false;
					} else {
						return false;
					}
				});
			},
			resetForm(formName) {
				this.$refs[formName].resetFields();
			},

			choice(k) {
				this.disabled = false /*  */
				this.hint = false /*  */
				/*  */
				for (let i = 0; i < this.bank_card.length; i++) {
					if (k == i) {
						this.bank_card[k].zfbk = true;
					} else {
						this.bank_card[i].zfbk = false;
						/*  */
						for (let j = 0; j < this.paymentList.length; j++) {
							this.paymentList[j].zfbk = false;
						}
					}
				}
			},
			terrace(k) {
				this.disabled = false /*  */
				this.hint = false /*  */
				for (var i = 0; i < this.paymentList.length; i++) {
					if (k == i) {
						this.paymentList[k].zfbk = true;
					} else {
						this.paymentList[i].zfbk = false;
						for (let j = 0; j < this.bank_card.length; j++) {
							this.bank_card[j].zfbk = false;
						}
					}
				}

			},
			// 
			mouseOver(k) {
				for (let i = 0; i < this.bankCardList.length; i++) {
					if (i == k) {
						this.bankCardList[k].operation = true;
					} else {
						this.bankCardList[i].operation = false;
					}
				}
			},
			// 
			mouseLeave() {
				for (let i = 0; i < this.bankCardList.length; i++) {
					this.bankCardList[i].operation = false;
				}
			},
			// 
			deleBankCard(k) {
				MessageBox.confirm('', '', {
					confirmButtonText: '',
					cancelButtonText: '',
					type: 'warning',
					center: true,
					lockScroll: false,
					closeOnClickModal: false,
				}).then(() => {
					this.bankCardList.splice(k, 1);
					let DCnum = 0;
					let CCnum = 0;
					for (let i = 0; i < this.bankCardList.length; i++) {
						if (this.bankCardList[i].type == "") {
							DCnum++
						}
						if (this.bankCardList[i].type == "") {
							CCnum++
						}
					}
					this.DCnum = DCnum;
					this.CCnum = CCnum;
					Message.success('!');
				}).catch(() => {

				});
			},
		}
	}
</script>

<style scoped>
	.my_account .el-main {
		display: flex;
		justify-content: center;
		text-align: left;
		padding-top: 50px;
		background-color: #f5f7fa;
	}

	.el-submenu .el-menu-item {
		min-width: 0;
	}

	/*  */
	.el-main .right {
		width: 860px;
		/* border: 1px solid firebrick; */
		margin-left: 20px;
	}

	/*  */
	.right .balance {
		padding: 25px 20px;
		background-color: #fff;
		margin-bottom: 30px;
		display: flex;
		align-items: center;
	}

	.balance .title {
		font-size: 18px;
		color: #999;
	}

	.balance .money {
		margin-left: 10px;
		margin-right: 50px;
		font-size: 25px;
	}

	.balance>button {
		background: none;
		border-color: #57BEC3;
		color: #57BEC3;
	}

	.balance .but_r {
		margin-right: 20px;
	}

	/*  */
	.el-tabs--border-card>>>.el-tabs__nav {
		width: 100%;
		text-align: center;
	}


	/*  */
	.el-tabs--border-card>>>.el-table .cell {
		text-align: center;
	}

	.el-tabs--border-card>>>.el-tabs__item:not(.is-disabled):hover {
		color: #57BEC3;
	}

	.el-tabs--border-card>>>.el-tabs__item.is-active {
		color: #57BEC3;
	}

	/*  */
	.PayPal .el-col-5 {
		border: 1px solid #57BEC3;
		margin: 10px 20px;
		height: 45px;
		display: flex;
		line-height: 45px;
		cursor: pointer;
		width: 25.83333%;

	}

	.PayPal .zfbk {
		border: 1px solid red;
	}

	.PayPal .el-col-5 img {
		height: 25px;
		width: 25px;
		margin: 10px 10px;
	}

	.PayPal span {
		color: red;
	}

	.PayPal .el-button--danger {

		width: 200px;
		margin: 10px 20px;
	}

	.PayPal>>>.el-tabs--border-card {
		background: none;
		border: none;
		box-shadow: none;
	}

	/*  */
	.bankCard {
		margin-top: 30px;
		background-color: #fff;
		padding: 25px 20px;
	}

	.bankCard>.el-row {
		margin-bottom: 20px;
	}

	.bankCard .title {
		font-size: 18px;
	}

	.bankCard .bankNum {
		font-size: 16px;
		color: #5e5e5e;
	}

	.bankCard .bankNum>span {
		color: red;
		padding: 0 3px;
	}

	.bankCard .bankCardList {
		margin-bottom: 20px;
	}

	.bankCard .bg-purple {
		border: 1px solid #DCDFE6;
	}

	.bankCard .bg-purple:hover {
		border-color: #57BEC3;
	}

	.bankCard .grid-content {
		border-radius: 4px;
		min-height: 36px;
		line-height: 36px;
	}

	/*  */
	.bankCard #bankCardBut {
		height: 188px;
		border: 1px solid #e2eaf2;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		cursor: pointer;
		font-size: 18px;
		border-radius: 4px;
	}

	.bankCard #bankCardBut:hover {
