<template>
	<div class="my_set">
		<el-container>
			<el-header>
				<headerNav />
			</el-header>
			<el-main>
				<pbLeft />
				<div class="right">
					<div class="firstModule" id="personalDataID">
						<div class="title">
							
						</div>
						<div class="form">
							<el-form ref="form" :model="form" label-width="80px">
								<el-form-item label="">
									<el-input v-model="form.name"></el-input>
								</el-form-item>
								<el-form-item label="">
									<el-col :span="8">
										<el-date-picker type="date" placeholder="" v-model="form.date1" style="width: 100%;"></el-date-picker>
									</el-col>
								</el-form-item>
								<el-form-item label="">
									<el-radio-group v-model="form.resource">
										<el-radio :label="1"></el-radio>
										<el-radio :label="0"></el-radio>
									</el-radio-group>
								</el-form-item>
								<el-form-item label="">
									<el-cascader v-model="defaultAddress" :options="defaultAptions" @change="handleChange"></el-cascader>
								</el-form-item>
								<el-form-item label="">
									<el-input type="textarea" show-word-limit maxlength="40" rows="4" v-model="form.desc"></el-input>
								</el-form-item>
								<el-form-item class="operationBut but">
									<el-button class="operationBut" type="primary" @click="onSubmit"></el-button>
								</el-form-item>
							</el-form>
						</div>
					</div>
					<div class="secondModule" id="receGoodsID">
						<div class="thead">
							<div class="addTitle">
								<span></span><span class="num" style="vertical-align: bottom;"> <span>{{addressNum}}</span>
									 <span>10</span> </span>
							</div>
							<div class="operationBut">
								<el-button class="operationBut" type="primary" @click="onAddress(1)" icon="el-icon-plus"></el-button>
							</div>
						</div>
						<div class="addressList">
							<el-row :gutter="20">
								<el-col :span="12" v-for="(val,k) in addresslist" :key="k">
									<div class="addressInfo" :class="{addressInfoBor:!val.showBut}">
										<div class="but">
											<span style="font-size: 18px;"><i class="el-icon-user-solid"></i>{{val.name}}</span>
											<el-button type="primary" size="small " v-if="val.showBut" class="defaultBut"></el-button>
											<el-button type="primary" size="small " v-if="!val.showBut" class="setdefaultBut" @click="defBut(k)"></el-button>
										</div>
										<div class="addRe">
											<i class="el-icon-s-home"></i>{{val.province+val.city+val.area+' '+val.detAddress}}
										</div>
										<div class="but">
											<div><i class="el-icon-phone"></i>{{val.phone}}</div>
											<div>
												<el-button type="primary" size="mini" @click="onAddress(0,k)"></el-button>
												<el-button type="primary" @click="deleAddress(k)" size="mini" slot="reference" v-if="!val.showBut">
													
												</el-button>
											</div>
										</div>
									</div>
								</el-col>
							</el-row>
						</div>
						<!--  -->
						<div class="addaddRess">
							<el-dialog :title="title" :lock-scroll="false" :close-on-click-modal="false" :visible.sync="dialogFormVisible">
								<el-form :model="addUpdaAddress" style="padding-bottom: 0;">
									<el-form-item label="" :label-width="formLabelWidth">
										<el-input placeholder="" v-model="addUpdaAddress.addUpdaName" autocomplete="off"></el-input>
									</el-form-item>
									<el-form-item label="" :label-width="formLabelWidth">
										<el-input placeholder="" v-model="addUpdaAddress.addUpdaPhone" autocomplete="off"></el-input>
									</el-form-item>
									<el-form-item label="" :label-width="formLabelWidth">
										<el-cascader v-model="address" :options="options"></el-cascader>
									</el-form-item>
									<el-form-item label="" :label-width="formLabelWidth">
										<el-input placeholder="" v-model="addUpdaAddress.addUpdaDetAddress" autocomplete="off"></el-input>
									</el-form-item>
									<el-form-item :label-width="formLabelWidth" v-if="showUpdateOn">
										<el-checkbox v-model="setDefault"></el-checkbox>
									</el-form-item>
								</el-form>
								<div slot="footer" class="dialog-footer">
									<el-button @click="dialogFormVisible = false"> </el-button>
									<el-button type="primary" @click="addUpdaConfirm"> </el-button>
								</div>
							</el-dialog>
						</div>
					</div>
					<!--  -->
					<div class="thirdModule" id="pwdID">
						<div class="title">
							
						</div>
						<div class="resetInfo">
							<el-form :model="updatePwdRuleForm" status-icon :rules="updatePwdRules" ref="updatePwdRuleForm" label-width="100px"
							class="demo-ruleForm">
								<el-form-item label="" prop="primaryPass">
									<el-input type="password" placeholder="" v-model="updatePwdRuleForm.primaryPass" autocomplete="off"></el-input>
								</el-form-item>
								<el-form-item label="" prop="newPass">
									<el-input type="password" placeholder="" v-model="updatePwdRuleForm.newPass" autocomplete="off"></el-input>
								</el-form-item>
								<el-form-item label="" prop="checkPass">
									<el-input type="password" placeholder="" v-model="updatePwdRuleForm.checkPass" autocomplete="off"></el-input>
								</el-form-item>
								<el-form-item class="operationBut">
									<el-button type="primary" @click="submitForm('updatePwdRuleForm')"></el-button>
								</el-form-item>
							</el-form>
						</div>
					</div>
				</div>
			</el-main>
		</el-container>
	</div>
</template>

<script>
	import areaInfo from '../utils/area.js';
	import headerNav from "../components/headerNav.vue";
	import pbLeft from "../components/pbLeft.vue";
	import {
		Header,
		Button,
		Input,
		Main,
		Container,
		// 
		Form,
		FormItem,
		Select,
		Option,
		Col,
		Row,
		DatePicker,
		TimePicker,
		Switch,
		CheckboxGroup,
		Checkbox,
		RadioGroup,
		Radio,
		// 
		Cascader,
		// 
		Popconfirm,
		// 
		Dialog,
		Message,
		MessageBox
	} from 'element-ui';
	export default {
		// created(){
		// 	console.log(areaInfo);
		// },
		// ngAfterViewInit(){
		// 	document.getElementById('pwdID').scrollIntoView();
		// },
		components: {
			headerNav,
			pbLeft,
			[Header.name]: Header,
			[Button.name]: Button,
			[Input.name]: Input,
			[Main.name]: Main,
			[Container.name]: Container,
			// 
			[Form.name]: Form,
			[FormItem.name]: FormItem,
			[Select.name]: Select,
			[Option.name]: Option,
			[Col.name]: Col,
			[Row.name]: Row,
			[DatePicker.name]: DatePicker,
			[TimePicker.name]: TimePicker,
			[Switch.name]: Switch,
			[CheckboxGroup.name]: CheckboxGroup,
			[Checkbox.name]: Checkbox,
			[RadioGroup.name]: RadioGroup,
			[Radio.name]: Radio,
			// 
			[Cascader.name]: Cascader,
			// 
			[Popconfirm.name]: Popconfirm,
			// 
			[Dialog.name]: Dialog,
			[Message.name]: Message,
			[MessageBox.name]: MessageBox,
 
		},
		mounted() {
			// 
			this.addressNum = this.addresslist.length;
		},
		data() {
			// 
			var validatePass = (rule, value, callback) => {
				if (value === '') {
					callback(new Error(''));
				} else {
					callback();
				}
			};
			var validatePass1 = (rule, value, callback) => {
				if (value === '') {
					callback(new Error(''));
				} else {
					if (this.updatePwdRuleForm.checkPass !== '') {
						this.$refs.updatePwdRuleForm.validateField('checkPass');
					}
					callback();
				}
			};
			var validatePass2 = (rule, value, callback) => {
				if (value === '') {
					callback(new Error(''));
				} else if (value !== this.updatePwdRuleForm.newPass) {
					callback(new Error('!'));
				} else {
					callback();
				}
			};
			return {
				// 
				title: '',
				// 
				setDefault: false,
				//   
				verCode: '',
				pwd: '',
				confirmPwd: '',
				primaryPwd: '',
				// 
				addressNum: '',
				// 
				dialogFormVisible: false,
				addUpdaAddress: {
					addUpdaName: '',
					addUpdaPhone: '',
					addUpdaDetAddress: '',
				},
				formLabelWidth: '120px',
				// 
				form: {
					name: '',
					date1: '2018-12-08',
					type: [],
					resource: 1,
					desc: '',
				},
				defaultAddress: ["", "", ""],
				defaultAptions: areaInfo,
				address: [],
				options: areaInfo,
				// 
				addresslist: [{
						name: '',
						showBut: true,
						province: '',
						city: '',
						area: '',
						detAddress: '217',
						phone: '18947544547'
					},
					{
						name: '',
						showBut: false,
						province: '',
						city: '',
						area: '',
						detAddress: ' 88',
						phone: '15685424514'
					},
					{
						name: '',
						showBut: false,
						province: '',
						city: '',
						area: '',
						detAddress: '',
						phone: '17516447544'
					},
					{
						name: '',
						showBut: false,
						province: '',
						city: '',
						area: '',
						detAddress: '',
						phone: '15987544551'
					},

				],
				// addUpdateN: '',
				// addUpdateK: ''
				// 
				showUpdateOn: true,
				// 
				updatePwdRuleForm: {
					primaryPass: '',
					newPass: '',
					checkPass: '',
				},
				updatePwdRules: {
					primaryPass: [{
						validator: validatePass,
						trigger: 'blur'
					}],
					newPass: [{
						newPass: [{
							validator: validatePass1,
							trigger: 'blur'
						}],
						validator: validatePass1,
						trigger: 'blur'
					}],
					checkPass: [{
						validator: validatePass2,
						trigger: 'blur'
					}],
				}
			};
		},
		methods: {
			onSubmit() {
				// console.log('submit!');
				Message.success('');
			},
			handleChange(value) {
				console.log(value);
			},
			// 
			defBut(k) {
				for (let i = 0; i < this.addresslist.length; i++) {
					if (k == i) {
						this.addresslist[k].showBut = true;
					} else {
						this.addresslist[i].showBut = false;
					}
				}
			},
			// 
			deleAddress(k) {
				MessageBox.confirm('', '', {
					confirmButtonText: '',
					cancelButtonText: '',
					type: 'warning',
					center: true,
					lockScroll: false,
					closeOnClickModal: false,
				}).then(() => {
					this.addresslist.splice(k, 1);
					this.addressNum = this.addresslist.length;
					Message.success('!');
				}).catch(() => {
					
				});
			},
			//  
			onAddress(n, k) {
				if (this.addresslist.length >= 10) {
					Message.error(' 10 ');
					return;
				}
				// 
				this.dialogFormVisible = true;
				// n
				this.addUpdateN = n;
				// k
				this.addUpdateK = k;

				// 
				if (n == 0) {
					// 
					this.title = "";
					this.address = [];
					this.addUpdaAddress.addUpdaName = this.addresslist[k].name;
					this.addUpdaAddress.addUpdaPhone = this.addresslist[k].phone;
					this.address[0] = this.addresslist[k].province;
					this.address[1] = this.addresslist[k].city;
					this.address[2] = this.addresslist[k].area;
					this.addUpdaAddress.addUpdaDetAddress = this.addresslist[k].detAddress;
					// 
					if (this.addresslist[k].showBut) {
						this.showUpdateOn = false;
					} else {
						this.showUpdateOn = true;
						this.setDefault = this.addresslist[k].showBut;
					}
				}
				if (n == 1) {
					// 
					this.title = "";
					this.address = [];
					this.addUpdaAddress = {};
					this.setDefault = false;
				}
			},
			//  
			addUpdaConfirm() {
				if (this.addUpdaAddress.addUpdaName == undefined || this.addUpdaAddress.addUpdaName == "") {
					Message.error('');
				} else if (this.addUpdaAddress.addUpdaPhone == undefined || this.addUpdaAddress.addUpdaPhone == "") {
					Message.error('');
				} else if (this.address.length == 0) {
					Message.error('');
				} else if (this.addUpdaAddress.addUpdaDetAddress == undefined || this.addUpdaAddress.addUpdaDetAddress == "") {
					Message.error('');
				} else {
					this.dialogFormVisible = false;
					// 
					if (this.addUpdateN == 1) {
						// 
						if (this.setDefault) {
							for (let i = 0; i < this.addresslist.length; i++) {
								this.addresslist[i].showBut = false;
							}
						}
						// 
						this.addresslist.push({
							name: this.addUpdaAddress.addUpdaName,
							phone: this.addUpdaAddress.addUpdaPhone,
							province: this.address[0],
							city: this.address[1],
							area: this.address[2],
							detAddress: this.addUpdaAddress.addUpdaDetAddress,
							showBut: this.setDefault
						});
						// 
						this.addressNum = this.addresslist.length;
					}
					// 
					if (this.addUpdateN == 0) {
						// 
						if (this.setDefault) {
							for (let i = 0; i < this.addresslist.length; i++) {
								this.addresslist[i].showBut = false;
							}
						}
						// 
						this.addresslist[this.addUpdateK].name = this.addUpdaAddress.addUpdaName;
						this.addresslist[this.addUpdateK].phone = this.addUpdaAddress.addUpdaPhone;
						this.addresslist[this.addUpdateK].province = this.address[0];
						this.addresslist[this.addUpdateK].city = this.address[1];
						this.addresslist[this.addUpdateK].area = this.address[2];
						this.addresslist[this.addUpdateK].detAddress = this.addUpdaAddress.addUpdaDetAddress;
						if (!this.addresslist[this.addUpdateK].showBut) {
							this.addresslist[this.addUpdateK].showBut = this.setDefault;
						}
					}
				}

			},
			// 
			submitForm(formName) {
				this.$refs[formName].validate((valid) => {
					if (valid) {
						// alert('submit!');
						Message.success('');
					} else {
						// console.log('error submit!!');
						return false;
					}
				});
			},

		}
	}
</script>

<style scoped>
	.my_set .el-main {
		display: flex;
		justify-content: center;
		text-align: left;
		padding-top: 50px;
		background-color: #f5f7fa;
	}


	.my_set .el-submenu .el-menu-item {
		min-width: 0;
	}

	/*  */
	.el-main .right {
		width: 860px;
		/* border: 1px solid red; */
		margin-left: 20px;
	}

	/*  */
	.right .title {
		margin-bottom: 30px;
		font-size: 18px;
	}


	.right>div {
		background-color: #fff;
		padding: 30px;
		margin-bottom: 50px;
	}

	/*  */
	.right .operationBut button {
		background-color: #f5f5f5;
		color: #666;
		border-color: #e1e1e1
	}

	.right .operationBut :hover {
		border-color: #57BEC3;
		color: #57BEC3;
		transition: 0.1s;
		border-radius: 4px;
	}

	/*  */


	/*  */
	.firstModule .form .but {
		margin-bottom: 0;
		display: flex;
		justify-content: flex-end;
	}


	.firstModule>>>.el-textarea .el-input__count {
		background: none;
	}

	/*  */
	.firstModule .form>>>.el-input__count {
		right: 25px;
	}

	/*  */


	.thead {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding-bottom: 30px;
	}

	.thead .addTitle {
		font-size: 18px;
	}

	.thead .addTitle .num {
		font-size: 15px;
		margin-left: 20px;
		vertical-align: bottom;
		color: grey;
	}

	.thead .addTitle .num span {
		color: #57BEC3;
		font-size: 18px;
	}

	/*  */

	.addressList .addressInfo {
		background: #fff;
		border-radius: 4px;
		margin-bottom: 30px;
		padding: 10px;
		border: 4px solid transparent;
		border-image: 5 repeating-linear-gradient(-45deg, #81cfd3 0, #57BEC3 10%, transparent 0, transparent 15%, #80b3e6 0, #5ba2ea 25%, transparent 0, transparent 30%);
	}

	.addressList .addressInfoBor {
		border-image: 5 repeating-linear-gradient(-45deg, #e9e9e9 0, #d0d0d0 10%, transparent 0, transparent 15%, #ccc 0, #ccc 25%, transparent 0, transparent 30%);
	}


	.addressList i {
		color: #ccc;
		margin-right: 5px;
	}

	.addressList .addressInfo>div {
		padding: 8px 10px;
	}

	/*  */
	.addressList .but button {
		background-color: #fafafa;
		border: 1px solid #c2c2c2;
		color: #333;
	}

	.addressList .but {
		display: flex;
		justify-content: space-between;
		align-items: center;
	}

	/*  */
	.addressList .but .defaultBut {
		background-color: #57BEC3;
		border-color: #57BEC3;
		color: #fff;
		opacity: 0.7;
	}

	/*  */
	.addressList .but .setdefaultBut {
		background-color: #f5f5f5;
		color: #666;
		border-color: #e1e1e1;
	}

	/*  */
	.addressList .addRe {
		height: 44px;
		word-break: break-all;
		display: -webkit-box;
		-webkit-line-clamp: 2;
		-webkit-box-orient: vertical;
		overflow: hidden;
	}

	/* form */
	.addaddRess>>>.el-dialog__body {
		padding-bottom: 0;
	}

	/*  */
	.thirdModule .resetInfo>.el-input {
		margin-bottom: 20px;
	}

	/*  */
	.thirdModule .resetInfo .operationBut {
		display: flex;
		justify-content: flex-end;
	}
</style>
<style>
	.el-popper .el-cascader-panel {
		height: 200px;
	}
</style>
