<script>
    import headerNav from "../../components/headerNav.vue";
    import {
        carousel,
        container,
        header,
        CarouselItem,
        row,
        button,
        CheckboxGroup,
        checkbox,
        divider
    } from 'element-ui';

    export default {
        data() {
            return {
                checked: false,
                checked1: false,
                checked2: false,
                checked3: false,
                checked4: false,
                id:this.$route.query.id,
                commodityInfo:[], 
                 
            };
        },
        components: {
            headerNav,
            [carousel.name]: carousel,
            [container.name]: container,
            [header.name]: header,
            [CarouselItem.name]: CarouselItem,
            [row.name]: row,
            [button.name]: button,
            [CheckboxGroup.name]: CheckboxGroup,
            [checkbox.name]: checkbox,
            [divider.name]: divider,
        },
        created(){
            let id = this.id;
            this.commodityFun(id)
        },
        methods:{
            commodityFun(id){
                let commodity = this.$store.state.commodity_List
                let commodityArr =[];
                commodity.some(item=>{
                    if(item.id==id){  
                        commodityArr=item; 
                        return true;
                    }
                })
                this.commodityInfo=commodityArr;
                 
            }, 
        }
    }
